.card-image {
  border-radius: "10px";
  height: "90%"; 
  opacity: 0.7;
  background-color: black; 
}
.card-image:after {
  content:'\A';
  position:absolute;
  width:100%; height:100%;
  top:0; left:0;
  background:rgba(0,0,0,0.6);
  opacity:0;
  transition: all 0.5s;
  -webkit-transition: all 0.5s; 
}
.card-image:hover:after {
  opacity:1;
  transition:  opacity 0.2s ease;
  display: block;
}
